
@charset "UTF-8";
/*标题*/
.index-title {
  background-color: white;
  color: #333333;
  display: block;
  font-size:  0.42667rem ;
  font-weight: 600;
  line-height:  0.512rem ;
  margin-top:  0.26667rem ;
  padding:  0.42667rem 0.26667rem 0rem ;
}
.toggle {
  display: none !important;
}

/*容器*/
.index-container {
  display: flex;
  background-color: white;
  border-bottom-color: #eeeeee;
  border-bottom-style: solid;
  border-bottom-width: 1px;
  line-height:  0.352rem ;
  position: relative;
  font-size:  0.29333rem ;
  color: #666666;
  flex-basis: auto;
  flex-grow: 0;
  flex-shrink: 0;
  /*容器右边*/
}
.index-container .shop {
    /*容器左边*/
}
.index-container .shop .index-logo {
      padding:  0.4rem 0.26667rem ;
      position: relative;
}
.index-container .shop .index-logo .logo-img {
        position: relative;
        height:  1.73333rem ;
}
.index-container .shop .index-logo .logo-img img {
          position: relative;
          display: block;
          height: 100%;
          width: 100%;
}
.index-container .index-main {
    padding:  0.4rem 0.26667rem 0.4rem 0rem ;
    width:  7.73333rem ;
    display: flex;
    justify-content: center;
    flex-direction: column;
    flex-grow: 1;
    line-height:  0.352rem ;
    color: #666666;
    /*商品名字,订单,等*/
    /*活动*/
}
.index-container .index-main .index-line {
      display: flex;
      justify-content: space-between;
      align-items: center;
}
.index-container .index-main .index-line h3 {
        color: #333333;
        font-size:  0.4rem ;
        height:  0.42667rem ;
        line-height:  0.48rem ;
        font-weight: bold;
}
.index-container .index-main .index-line:nth-child(2) {
        margin-top:  0.2rem ;
}
.index-container .index-main .index-line:nth-child(3) {
        margin-top:  0.24rem ;
}
.index-container .index-main .index-line .index-moneylimit span:nth-child(1)::after {
        content: '|';
        margin:  0.08rem ;
        height:  0.4rem ;
}
.index-container .index-main .index-line .index-delivery {
        color: white;
        background-color: #44a5ff;
}
.index-container .index-main .dotted {
      border: dashed 0.5px #cccccc;
      margin:  0.2rem 0rem ;
}
.index-container .index-main .index-activities {
      font-size:  0.29333rem ;
      line-height: 26.4px;
      position: relative;
}
.index-container .index-main .index-activities .index-btn {
        position: absolute;
        right: 0;
        top: 0;
        height: 100%;
        color: #999;
        line-height:  0.01333rem ;
}



