
@charset "UTF-8";
/*头部*/
#msite {
  height: 100%;
  /*搜索*/
  /*热词*/
  /*列表*/
  /*加载圈*/
}
#msite .content {
    font-size:  0.32rem ;
    line-height:  0.384rem ;
    background-image: linear-gradient(90deg, #0af, #0085ff);
    padding:  0.26667rem 0.37333rem 0rem ;
}
#msite .content .smallContent {
      display: flex;
      justify-content: space-between;
      align-items: center;
      color: white;
      font-size:  0.32rem ;
      height:  0.92rem ;
      line-height:  0.384rem ;
}
#msite .content .smallContent .left {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width:  5.54667rem ;
}
#msite .content .smallContent .left span {
          font-size:  0.45333rem ;
          margin:  0rem/px 0.13333rem ;
          line-height:  0.544rem ;
          font-weight: bold;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
}
#msite .content .smallContent .left i:nth-child(1) {
          font-size:  0.53333rem ;
          padding: 0 10px;
}
#msite .content .smallContent aside {
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: white;
        line-height:  0.384rem ;
        text-align: right;
}
#msite .content .smallContent aside .weather h2 {
          font-size:  0.37333rem ;
          line-height:  0.448rem ;
}
#msite .content .smallContent aside .weather p {
          line-height:  0.32rem ;
          font-size:  0.26667rem ;
}
#msite .content .smallContent aside img {
          width:  0.73333rem ;
}
#msite .search {
    position: sticky;
    top: 0;
    z-index: 999;
    color: #333333;
    line-height:  0.384rem ;
    background-image: linear-gradient(90deg, #0af, #0085ff);
    margin:  -0.01333rem 0rem ;
    padding:  0.2rem 0.37333rem ;
}
#msite .search a {
      display: flex;
      justify-content: center;
      align-items: center;
      width:  9.33333rem ;
      height:  0.96rem ;
      padding-top: 10px;
      background-color: white;
}
#msite .search a span {
        color: #666666;
        font-size:  0.34667rem ;
        font-weight: bold;
        line-height:  0.41333rem ;
        align-items: center;
}
#msite .search a i {
        font-size: 20px;
}
#msite .hot-word {
    background-image: linear-gradient(90deg, #0af, #0085ff);
    display: flex;
    justify-content: space-around;
    align-items: center;
    height:  0.96rem ;
}
#msite .hot-word a {
      font-size: 20px;
      color: white;
      margin-right: 15px;
}
#msite .lunbo {
    position: relative;
}
#msite .lunbo .entry {
      position: relative;
      background-color: white;
      height:  4.72rem ;
      overflow: hidden;
}
#msite .lunbo .entry .list1, #msite .lunbo .entry .list2 {
        position: absolute;
}
#msite .lunbo .entry .list1 a, #msite .lunbo .entry .list2 a {
          text-align: center;
          font-size:  0.32rem ;
          line-height:  0.384rem ;
          display: inline-block;
          padding-top:  0.29333rem ;
          width:  2.5rem ;
}
#msite .lunbo .entry .list1 a img, #msite .lunbo .entry .list2 a img {
            width:  1.2rem ;
}
#msite .lunbo .entry .list2 {
        position: absolute;
        transform: translateX( 10rem );
}
#msite .lunbo .dots {
      position: absolute;
      left: 50%;
      bottom: 10px;
      transform: translateX(-50%);
}
#msite .lunbo .dots .dot {
        width: 8px;
        height: 8px;
        font-size:  0.05333rem ;
}
#msite .lunbo .dots .dot.active {
          color: #00aaff;
}
#msite .loaded {
    background: yellow;
    text-align: center;
    height:  0.66667rem ;
    line-height:  0.66667rem ;
    border: 1px solid red;
    padding-bottom:  2rem ;
}



