
@charset "UTF-8";
footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height:  1.33333rem ;
  width: 100%;
  text-align: center;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 999;
  color: #333333;
  background-color: white;
  padding-top:  0.12rem ;
  line-height:  0.384rem ;
}
footer .fa {
    font-size:  0.53333rem ;
}
footer span {
    font-size:  0.26667rem ;
    line-height:  0.32rem ;
    display: block;
    color: #666666;
}
footer a {
    flex: 1;
    /*line-height: pxToRem(28.8px);*/
    /*方法1:通过切换类*/
    /*方法2:通过Vue添加的类名,改变字体颜色,此方法比较简单*/
}
footer a.active {
      color: #0089dc;
}
footer a.router-link-exact-active.router-link-exact-active {
      color: #0089dc;
}



